import { TreeView, TreeItem } from "@material-ui/lab";
import { MdExpandLess, MdExpandMore } from "react-icons/md";
import uuid from "uuid/v4";

const toTreeItems = object => {
  const entries = Object.entries(object);
  return entries.map(([key, value]) => {
    switch (typeof value) {
      case "number":
        return (
          <TreeItem nodeId={uuid()} label={key}>
            <TreeItem nodeId={uuid()} label={value.toString()} />
          </TreeItem>
        );
      case "string":
        return (
          <TreeItem nodeId={uuid()} label={key}>
            <TreeItem nodeId={uuid()} label={value} />
          </TreeItem>
        );
      case "object":
        return (
          <TreeItem nodeId={uuid()} label={key}>
            {toTreeItems(value)}
          </TreeItem>
        );
      default:
        return (
          <TreeItem nodeId={uuid()} label={key}>
            <TreeItem nodeId={uuid()} label={`UNKNOWN TYPE: ${typeof value}`} />
          </TreeItem>
        );
    }
  });
};

export const ObjectTree = ({ object }) => (
  <TreeView
    defaultCollapseIcon={<MdExpandMore />}
    defaultExpandIcon={<MdExpandLess />}
  >
    {toTreeItems(object)}
  </TreeView>
);
